import React, { useEffect, useState } from "react";

import { getResources } from "../../api/contenful/utils.js";

import IndexItem from "./IndexList/IndexList.js";
import Resource from "./Resource/Resource.js";
import styles from "./garden.module.css";
import SearchBar from "./SearchBar/SearchBar.js";
import Footer from "../../components/Footer/Footer.js";

const GardenPage = () => {
  const [searchString, setSearchString] = useState(null);
  const [gardenItems, setGardenItems] = useState([]);
  const [filteredItems, setFilteredItems] = useState([]);
  const [counts, setCounts] = useState({});
  const [activeIndex, setActiveIndex] = useState(null);

  const [mobileView, setMobileView] = useState(false);

  const handleFilter = (tag, index) => {
    if (tag === "All") {
      setFilteredItems(gardenItems);
    } else {
      const filtered = gardenItems.filter((item) => {
        return item.contentfulMetadata.tags.some((tagObj) => tagObj.id === tag);
      });
      setFilteredItems(filtered);
    }
    setActiveIndex(index); // Update the active index when an item is clicked
  };

  useEffect(() => {
    getResources()
      .then((response) => {
        const items = response.resourcesCollection.items;
        setGardenItems(items);
        setFilteredItems(items);
        // Initialize counts with zeros
        const initialCounts = {
          all: 0,
          podcasts: 0,
          films: 0,
          academic: 0,
          publications: 0,
          climateScience: 0,
          exhibitions: 0,
          events: 0,
          projects: 0,
          workshops: 0,
          festivals: 0,
          art: 0,
          studios: 0,
          collectivesAndNetworks: 0,
          firstNations: 0,
          organisations: 0,
          businesses: 0,
          conferences: 0,
          tools: 0,
          learningDatabases: 0,
          wellbeing: 0,
          news: 0,
          shopping: 0,
        };

        setCounts(initialCounts);

        // Create a copy of counts
        const updatedCounts = { ...initialCounts };

        // Update the counts
        items.forEach((item) => {
          const tags = item.contentfulMetadata.tags;
          tags.forEach((tagObj) => {
            const text = tagObj.name;
            updatedCounts[text]++;
          });
        });

        // Update the state with the modified counts
        setCounts(updatedCounts);

        if (searchString) {
          const filteredItems = items.filter((item) => {
            return item.title
              .toLowerCase()
              .includes(searchString.toLowerCase());
          });
          setFilteredItems(filteredItems);
        }
      })
      .catch((error) => {
        console.error("Error fetching resources:", error);
      });
  }, [searchString]);

  useEffect(() => {
    // Check the viewport width to determine mobile view
    const handleResize = () => {
      if (window.innerWidth < 970) {
        setMobileView(true);
      } else {
        setMobileView(false);
      }
    };

    handleResize(); // Check on initial render

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const [showDropdown, setShowDropdown] = useState(false);

  return (
    <>
      <div className={styles["outer"]}>
        <div className={styles["container"]}>
          <div className={styles["column"]}>
            <div className={styles["digital-garden"]}>
              <div className={styles["gardenMenu"]}>
                {mobileView ? (
                  <div>
                    <SearchBar
                      searchString={searchString}
                      setSearchString={setSearchString}
                    />
                    {/* <div
                      className={styles["drop-down"]}
                      onClick={() => setShowDropdown(!showDropdown)}
                    > */}
                    {/* <div className={styles["ddMenu"]}>
                        <div>Select Filters</div>
                        <div className={styles["plus"]}>+</div>
                      </div>

                      {showDropdown && (
                        <div className={styles["dropdownContent"]}>
                 
                        </div>
                      )} */}
                    {/* </div> */}
                  </div>
                ) : (
                  <>
                    <SearchBar
                      searchString={searchString}
                      setSearchString={setSearchString}
                    />
                  </>
                )}
              </div>

              <div className={styles["gardenOuter"]}>
                <div className={styles["gardenContent"]}>
                  {filteredItems.map((item, index) => (
                    <Resource key={index} item={item} />
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
        <p>
          Submit to the growing Digital Garden{" "}
          <a
            href="https://docs.google.com/forms/d/e/1FAIpQLSew1Yc3XcHr4RqJGXIcHZmzDJQUEr5dwvdzyBEPlVK-vK75TA/viewform"
            target="_blank"
          >
            here
          </a>
          .{" "}
        </p>
      </div>
      <Footer />
    </>
  );
};

export default GardenPage;
